<template>
  <MainNav />
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-8">
        <div class="card mt-5" style="width: 100%">
          <div class="card-body">
            <h5 class="card-title">Submit A Joke</h5>
            <form @submit.prevent="saveJoke()">
              <div class="mb-3">
                <select
                  class="form-select"
                  aria-label="Default select example"
                  name="joke_type"
                  v-model="form.joke_type"
                >
                  <option value="">Select Joke Type</option>
                  <option
                    v-for="joke in joke_types"
                    v-bind:key="joke.type"
                    :value="joke.type"
                  >
                    {{ joke.type }}
                  </option>
                </select>
                <p v-if="!form.joke_type" class="fs-6 text-danger float-start">
                  Please select a joke type
                </p>
              </div>
              <div class="mb-3">
                <input
                  type="text"
                  name="setup"
                  v-model="form.setup"
                  class="form-control"
                  id="exampleFormControlInput1"
                  placeholder="Enter joke setup"
                />
                <p v-if="!form.setup" class="fs-6 text-danger float-start">
                  Please input the joke setup
                </p>
              </div>
              <div class="mb-3">
                <textarea
                  class="form-control"
                  name="punchline"
                  v-model="form.punchline"
                  id="exampleFormControlTextarea1"
                  placeholder="Enter Punchline"
                  rows="3"
                ></textarea>
                <p v-if="!form.punchline" class="fs-6 text-danger float-start">
                  Please enter the joke punchline
                </p>
              </div>
              <button type="submit" class="btn btn-primary btn-joke float-end">
                Submit Joke
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import MainNav from "./MainNav.vue";
import axios from "axios";
import Swal from "sweetalert2";
window.Swal = Swal;
const Toast = Swal.mixin({
  toast: true,
  position: "top-end",
  showConfirmButton: false,
  timer: 3000,
});
window.Toast = Toast;
export default {
  name: "SubmitJoke",
  components: {
    MainNav,
  },
  data() {
    return {
      joke_types: {},
      form: {
        joke_type: "",
        setup: "",
        punchline: "",
      },
    };
  },
  created() {
    this.getJokeTypes();
  },
  methods: {
    getJokeTypes() {
      axios.get("/get-joke-types").then((response) => {
        this.joke_types = response.data.data;
      });
    },
    saveJoke() {
      if (this.form.joke_type && this.form.setup && this.form.punchline) {
        axios.post("/save-joke", this.form).then((response) => {
          Toast.fire({
            position: "top-end",
            icon: "success",
            title: "Joke Successfuly Submitted",
            showConfirmButton: false,
            timer: 1500,
          });
          this.$router.push({ name: "Home" });
          this.clearForm();
          console.log(response);
        });
      } else {
        Toast.fire({
          icon: "error",
          title: "You have errors on your form.",
        });
      }
    },
    clearForm() {
      this.form.joke_type = "";
      (this.form.setup = ""), (this.form.punchline = "");
    },
  },
};
</script>
