<template>
  <MainNav />
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-6">
        <div class="card mt-5" style="width: 100%">
          <div class="card-body">
            <h5 class="card-title">Moderator Login</h5>
            <form @submit.prevent="login()">
              <div class="mb-3">
                <input
                  type="email"
                  name="email"
                  v-model="form.email"
                  class="form-control"
                  id="exampleFormControlInput1"
                  placeholder="Enter Email"
                />
                <p v-if="!form.email" class="fs-6 text-danger float-start">
                  Please enter full name
                </p>
              </div>
              <div class="mb-3">
                <input
                  type="password"
                  name="password"
                  v-model="form.password"
                  class="form-control"
                  id="exampleFormControlInput1"
                  placeholder="Enter Password"
                />
                <p v-if="!form.password" class="fs-6 text-danger float-start">
                  Please enter password
                </p>
              </div>

              <button type="submit" class="btn btn-primary btn-joke float-end">
                Login
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import MainNav from "./MainNav.vue";
import axios from "axios";
import Swal from "sweetalert2";
window.Swal = Swal;
const Toast = Swal.mixin({
  toast: true,
  position: "top-end",
  showConfirmButton: false,
  timer: 3000,
});
window.Toast = Toast;
export default {
  name: "SubmitJoke",
  components: {
    MainNav,
  },
  data() {
    return {
      form: {
        email: "",
        password: "",
      },
    };
  },
  methods: {
    login() {
      if (this.form.email && this.form.password) {
        axios.post("/login", this.form).then((response) => {
          localStorage.setItem("token", response.data.token);
          localStorage.setItem("user", JSON.stringify(response.data.user));
          this.$router.push({ name: "Dashboard" });
          this.clearForm();
        });
      } else {
        Toast.fire({
          icon: "error",
          title: "You have errors on your form.",
        });
      }
    },
    clearForm() {
      (this.form.email = ""), (this.form.password = "");
    },
  },
};
</script>
