<template>
  <MainNav />
  <div class="container">
    <div v-if="user.is_approved == 1" class="row justify-content-center">
      <div class="col-12">
        <div class="card mt-5" style="width: 100%">
          <div class="card-body">
            <div class="mb-4">
              <h5 class="card-title float-start">All Jokes</h5>
              <!-- Button trigger modal -->
              <button
                type="button"
                class="btn btn-primary float-end"
                @click.prevent="redirectToSubmitJoke()"
              >
                Add New Joke
              </button>
            </div>

            <table class="table table-striped table-hover">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Joke Type</th>
                  <th scope="col">Joke Setup</th>
                  <th scope="col">Joke Punchline</th>
                  <th scope="col">Is Approved</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="joke in jokes" v-bind:key="joke.id">
                  <th scope="row">{{ joke.joke_id }}</th>
                  <td>{{ joke.type }}</td>
                  <td>
                    <div class="text-wrap" style="width: 15rem">
                      {{ joke.setup }}
                    </div>
                  </td>
                  <td>{{ joke.punchline }}</td>
                  <td>
                    <span
                      v-if="joke.is_approved == 0"
                      class="badge bg-warning text-dark"
                      >False</span
                    >
                    <span v-else class="badge bg-success text-dark">True</span>
                  </td>
                  <td>
                    <a
                      class="btn btn-sm btn-success mx-2"
                      href="javascript:void(0)"
                      @click="editJoke(joke)"
                      >Edit</a
                    >
                    <a
                      class="btn btn-sm btn-danger"
                      href="javascript:void(0)"
                      @click="deleteJoke(joke.joke_id)"
                      >Delete</a
                    >
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="row justify-content-center">
      <div class="col-8">
        <div class="card mt-5" style="width: 100%">
          <div class="card-body">
            <img
              class="mb-3"
              style="width: 50%"
              src="../assets/not_found.svg"
            />
            <h5>Sorry your account is not verified yet</h5>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- Modal -->
  <div
    class="modal fade"
    id="jokeModal"
    tabindex="-1"
    aria-labelledby="jokeLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">Update Joke</h1>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <div class="modal-body">
          <form @submit.prevent="updateJoke()">
            <div class="mb-3">
              <select
                class="form-select"
                aria-label="Default select example"
                name="joke_type"
                v-model="form.joke_type"
              >
                <option
                  v-for="joke in joke_types"
                  v-bind:key="joke.id"
                  :value="joke.type"
                >
                  {{ joke.type }}
                </option>
              </select>
              <p v-if="!form.joke_type" class="fs-6 text-danger float-start">
                Please select a joke type
              </p>
            </div>
            <div class="mb-3">
              <input
                type="text"
                name="setup"
                v-model="form.setup"
                class="form-control"
                id="exampleFormControlInput1"
                placeholder="Enter joke setup"
              />
              <p v-if="!form.setup" class="fs-6 text-danger float-start">
                Please input the joke setup
              </p>
            </div>
            <div class="mb-3">
              <textarea
                class="form-control"
                name="punchline"
                v-model="form.punchline"
                id="exampleFormControlTextarea1"
                placeholder="Enter Punchline"
                rows="3"
              ></textarea>
              <p v-if="!form.punchline" class="fs-6 text-danger float-start">
                Please enter the joke punchline
              </p>
            </div>
            <div class="mb-3">
              <select
                class="form-select"
                aria-label="Default select example"
                name="is_approved"
                v-model="form.is_approved"
              >
                <option value="1">Approved</option>
                <option value="0">Rejected</option>
              </select>
              <p v-if="!form.joke_type" class="fs-6 text-danger float-start">
                Please approve or reject this joke
              </p>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-bs-dismiss="modal"
          >
            Close
          </button>
          <button
            type="button"
            class="btn btn-primary"
            @click.prevent="updateJoke()"
          >
            <span>Update</span>
          </button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import MainNav from "./MainNav.vue";
import axios from "axios";
import * as bootstrap from "bootstrap";
import Swal from "sweetalert2";
window.Swal = Swal;
const Toast = Swal.mixin({
  toast: true,
  position: "top-end",
  showConfirmButton: false,
  timer: 3000,
});
window.Toast = Toast;
window.bootstrap = bootstrap;
export default {
  name: "Dash-board",
  components: {
    MainNav,
  },
  data() {
    return {
      edit: false,
      joke_types: {},
      jokes: {},
      user: "",
      form: {
        id: "",
        joke_type: "",
        setup: "",
        punchline: "",
        is_approved: "",
      },
    };
  },
  created() {
    this.getAllJokes();
    this.getJokeTypes();
  },
  mounted() {
    let data = JSON.parse(localStorage.getItem("user"));
    if (data) {
      this.user = data;
    }
  },
  methods: {
    getAllJokes() {
      axios.get(" /get-all-jokes").then((response) => {
        this.jokes = response.data.data;
      });
    },
    getJokeTypes() {
      axios.get(" /get-joke-types").then((response) => {
        this.joke_types = response.data.data;
      });
    },
    saveJoke() {
      axios.post("/save-joke-md", this.form).then((res) => {
        console.log(res);
      });
      axios.post(" /save-joke", this.form).then((response) => {
        console.log(response);
        this.clearForm();
      });
    },
    editJoke(joke) {
      this.edit = true;
      let modal = bootstrap.Modal.getOrCreateInstance(
        document.getElementById("jokeModal")
      );
      modal.show();
      this.fillJokeData(joke);
      // modal.hide();
    },
    redirectToSubmitJoke() {
      this.$router.push({ name: "SubmitJoke" });
    },
    fillJokeData(joke) {
      this.form.joke_type = joke.type;
      this.form.setup = joke.setup;
      this.form.punchline = joke.punchline;
      this.form.is_approved = joke.is_approved;
      this.form.id = joke.joke_id;
    },
    updateJoke() {
      if (
        this.form.joke_type &&
        this.form.setup &&
        this.form.punchline &&
        this.form.is_approved
      ) {
        const token = localStorage.getItem("token");
        console.log(token);
        const config = {
          headers: {
            "Content-Type": "application/json",
            Authorization: token,
          },
        };
        axios
          .put(" /edit-joke", this.form, config)
          .then((response) => {
            let modal = bootstrap.Modal.getOrCreateInstance(
              document.getElementById("jokeModal")
            );
            modal.hide();
            this.getAllJokes();
            console.log(response);
            this.clearForm();
            Toast.fire({
              icon: "success",
              title: "The joke has been updated successfully.",
            });
          })
          .catch((error) => {
            console.log(error);
            Swal.fire({
              icon: "error",
              title: "Something went wrong.",
            });
          });
      } else {
        Toast.fire({
          icon: "error",
          title: "You have errors on your form.",
        });
      }
    },
    deleteJoke(id) {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!",
      }).then((result) => {
        if (result.value) {
          const token = localStorage.getItem("token");
          console.log(token);

          axios.delete(` /delete-joke`, {
            headers: {
              Authorization: token,
            },
            data: {
              id: id,
            },
          });
          Swal.fire("Deleted!", "The joke has been deleted.", "success");
          this.getAllJokes();
        }
      });
    },
    clearForm() {
      this.form.joke_type = "";
      (this.form.setup = ""), (this.form.punchline = "");
    },
  },
};
</script>
