<template>
  <MainNav />
  <div class="container">
    <div v-if="user.is_approved == 1" class="row justify-content-center">
      <div class="col-12">
        <div class="card mt-5" style="width: 100%">
          <div class="card-body">
            <div class="mb-4">
              <h5 class="card-title float-start">All Users</h5>
            </div>

            <table class="table table-striped table-hover">
              <thead>
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Name</th>
                  <th scope="col">Email</th>
                  <th scope="col">Is Approved</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="user in users" v-bind:key="user.user_id">
                  <th scope="row">{{ user.user_id }}</th>
                  <td>{{ user.fullname }}</td>
                  <td>
                    <div class="text-wrap" style="width: 15rem">
                      {{ user.email }}
                    </div>
                  </td>
                  <td>
                    <span
                      v-if="user.is_approved == 0"
                      class="badge bg-warning text-dark"
                      >False</span
                    >
                    <span v-else class="badge bg-success text-dark">True</span>
                  </td>
                  <td>
                    <a
                      v-if="user.is_approved == 0"
                      class="btn btn-sm btn-success mx-2"
                      href="javascript:void(0)"
                      @click="editUser(user)"
                      >Approve</a
                    >
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="row justify-content-center">
      <div class="col-8">
        <div class="card mt-5" style="width: 100%">
          <div class="card-body">
            <img
              class="mb-3"
              style="width: 50%"
              src="../assets/not_found.svg"
            />
            <h5>Sorry your account is not verified yet</h5>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import MainNav from "./MainNav.vue";
import axios from "axios";
import * as bootstrap from "bootstrap";
import Swal from "sweetalert2";
window.Swal = Swal;
const Toast = Swal.mixin({
  toast: true,
  position: "top-end",
  showConfirmButton: false,
  timer: 3000,
});
window.Toast = Toast;
window.bootstrap = bootstrap;
export default {
  name: "AllUsers",
  components: {
    MainNav,
  },
  data() {
    return {
      users: {},
      user: "",
      form: {
        user_id: "",
      },
    };
  },
  created() {
    this.getAllUsers();
  },
  mounted() {
    let data = JSON.parse(localStorage.getItem("user"));
    if (data) {
      this.user = data;
    }
  },
  methods: {
    getAllUsers() {
      axios.get("/get-all-users").then((response) => {
        this.users = response.data.data;
      });
    },
    editUser(user) {
      this.form.user_id = user.user_id;
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        type: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, approve this user!",
      }).then((result) => {
        if (result.value) {
          const token = localStorage.getItem("token");
          console.log(token);
          const config = {
            headers: {
              "Content-Type": "application/json",
              Authorization: token,
            },
          };
          axios
            .put("/edit-user", this.form, config)
            .then((response) => {
              console.log(response);
              this.getAllUsers();
              Toast.fire({
                icon: "success",
                title: "The user has been approved successfully.",
              });
            })
            .catch((error) => {
              console.log(error);
              Swal.fire({
                icon: "error",
                title: "Something went wrong.",
              });
            });
        }
      });
    },
  },
};
</script>
