import GetRandomJoke from './components/GetRandomJoke.vue'
import SubmitJoke from './components/SubmitJoke.vue'
import SignUp from './components/SignUp.vue'
import Login from './components/Login.vue'
import Dashboard from './components/Dashboard.vue'
import AllUsers from './components/Users.vue'
import { createRouter, createWebHistory } from 'vue-router'
const routes = [
    {
        name: 'Home',
        component: GetRandomJoke,
        path: '/'
    },
    {
        name: 'SubmitJoke',
        component: SubmitJoke,
        path: '/submit-joke'
    },
    {
        name: 'SignUp',
        component: SignUp,
        path: '/sign-up'
    },
    {
        name: 'Login',
        component: Login,
        path: '/login'
    },
    {
        name: 'Dashboard',
        component: Dashboard,
        path: '/dashboard'
    },
    {
        name: 'AllUsers',
        component: AllUsers,
        path: '/users'
    },
];
const router = createRouter({
    history: createWebHistory(),
    routes
})
export default router;