<template>
  <MainNav />
  <div class="container">
    <div class="row justify-content-center">
      <div class="col-8">
        <div class="card mt-5" style="width: 100%">
          <div class="card-body">
            <h5 class="card-title">Get Random Joke</h5>
            <div class="alert alert-success" role="alert" v-if="random_joke">
              <h4 class="alert-heading">{{ random_joke["type"] }} Joke!</h4>
              <p>{{ random_joke["setup"] }}</p>
              <hr />
              <p class="mb-0">{{ random_joke["punchline"] }}</p>
            </div>
            <form @submit.prevent="getRandomJoke">
              <div class="mb-3">
                <select
                  class="form-select"
                  aria-label="Default select example"
                  name="joke_type"
                  v-model="form.joke_type"
                >
                  <option value="">Select Joke Type</option>
                  <option
                    v-for="joke in joke_types"
                    v-bind:key="joke.type"
                    :value="joke.type"
                  >
                    {{ joke.type }}
                  </option>
                </select>
                <p v-if="!form.joke_type" class="fs-6 text-danger float-start">
                  Please select a joke type
                </p>
              </div>
              <button type="submit" class="btn btn-primary btn-joke float-end">
                Get Joke
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import MainNav from "./MainNav.vue";
import axios from "axios";
import Swal from "sweetalert2";
window.Swal = Swal;
const Toast = Swal.mixin({
  toast: true,
  position: "top-end",
  showConfirmButton: false,
  timer: 3000,
});
window.Toast = Toast;
export default {
  name: "GetRandomJoke",
  components: {
    MainNav,
  },
  data() {
    return {
      joke_types: {},
      random_joke: "",
      form: {
        joke_type: "",
      },
    };
  },
  created() {
    this.getJokeTypes();
  },
  methods: {
    getJokeTypes() {
      axios.get("/get-joke-types").then((response) => {
        this.joke_types = response.data.data;
      });
    },
    getRandomJoke() {
      //hii ndio ya kuget random joke...
      if (this.form.joke_type) {
        axios.post("/get-random-joke", this.form).then((response) => {
          this.random_joke = response.data;
        });
      } else {
        Toast.fire({
          icon: "error",
          title: "You have errors on your form.",
        });
      }
    },
  },
};
</script>
