import { createApp } from 'vue'
import App from './App.vue'
import router from './routers'

// Sweet Alert
import Swal from 'sweetalert2'
window.Swal = Swal
const Toast = Swal.mixin({
	toast: true,
	position: 'top-end',
	showConfirmButton: false,
	timer: 3000
})
window.Toast = Toast

createApp(App).use(router).mount('#app')
